import React from 'react';
import SearchNewsItem from './SearchNewsItem'
import logo from  './logo-exxaro.jpg'
import Footer from './Footer'

const SearchNewsList = ({ news }) => {
  console.log("Inside Search News") 
  //console.log(news)
 
  
  var styles ={
    wrap: {
      width: "40%",
      //position: "absolute",
      //top: "50%",
      //left: "50%",
      //transform: "translate(-50%, -50%)",
    },
   }
  return (
    <div>
     <div>  
      <p>News</p>
      </div>
    <ul>
      {
        news &&
          news.length > 0 ?
            (
               news.map(newsObject => {
                let article = {
                  title : newsObject.name,
                  url: newsObject.url,
                  image: newsObject.image,
                  description: newsObject.description
                }

                return (
                   // title, description, url, urlToImage
                    <SearchNewsItem key = {newsObject.url} article ={article}/>
                )
              })
            )
            :
            (
              <div/>
            )
      }
   </ul>
   </div>
  )
}

export default SearchNewsList