import React, {Component} from 'react';
import axios from 'axios';
import Input from './Input';
import ListTodo from './ListTodo';
import Iframe from 'react-iframe'
import logo from  './logo-exxaro.jpg'
import Header from '../Header'
import Footer from './Footer'
import SearchNewsList from './SearchNewsList'

class Todo extends Component {

  state = {
    todos: [],
    news: [],
    loading: false
  }

  /*
  componentDidMount(){
    if(this.state.query!="")
    {
      this.getTodos(this.state.query)
    }
  }
*/

 
  getTodos= ( searchquery) => {
    this.setState({
      query: searchquery,
      loading: true
    })
    axios.get('/api/search',  {
      params: {
        query: searchquery
      }
    })
      .then(res => {
        console.log(res.data)
        if(res.data['webPages']!=undefined){
          this.setState({
            loading: false,
            todos: res.data['webPages'].value
          })
        }
        if(res.data['news']!=undefined){
          console.log("getting News")
          this.setState({
            news: res.data['news'].value
          })
        }
      })
      .catch(err => console.log(err))
  }

  deleteTodo = (id) => {

    axios.delete(`/api/todos/${id}`)
      .then(res => {
        if(res.data){
          this.getTodos()
        }
      })
      .catch(err => console.log(err))
  }
  
  
  render() {
    var styles ={
      wrap: {
        width: "40%",
        //position: "absolute",
        //top: "50%",
        //left: "50%",
        //transform: "translate(-50%, -50%)",
      },
    }
            
    let { todos } = this.state;
    let {news} = this.state;
    
    return(   
      <div>
        <Header/>
       {
        todos &&
          todos.length > 0 ?
            (
              <div>
              <Input getTodos={this.getTodos}/>
              
              <ListTodo todos={todos} deleteTodo={this.deleteTodo}/>
              <SearchNewsList news= {news}/>
             <Footer/>
              </div>
            )
            :
            (
              <div >
             <Footer/>
              <Input getTodos={this.getTodos}/>
              {(this.state.loading)
                ?<div>
                    <p> Loading...</p>
                </div> 
                :<div/>
                } 
               </div>
            )
      }
      </div>
      
    )
  }
}

export default Todo;