import React, { Component } from 'react';
import axios from 'axios';
import Style from 'style-it';
import SearchField from 'react-search-field';
import searchlogo from './icons8-search-24.png'
class Input extends Component {

  state = {
    action: ""
  }

  addTodo = () => {
    const task = {action: this.state.action, userId: "Dev"}
    console.log(task)
    if(task.action && task.action.length > 0){
      axios.post('/api/topic', task)
        .then(res => {
          if(res.data){
            this.props.getTodos();
            this.setState({action: ""})
          }
        })
        .catch(err => alert(err))
    }else {
      alert('input field required')
    }
  }

  getSearchResults = () =>{
    const { getTodos } = this.props;
    getTodos(this.state.action)
    //this.addTodo();
  }

  handleKeyPress = (e)=>{
    if(e.key=='Enter'){
      const { getTodos } = this.props;
      getTodos(this.state.action)
      //this.addTodo();
    }
  }

  handleChange = (e) => {
    this.setState({
      action: e.target.value
    })
  }

  render() {
    let { action } = this.state;

    
    return Style.it(
      ` @import url(https://fonts.googleapis.com/css?family=Open+Sans);

      
      
      .search {
        width: 100%;
        position: relative;
        display: flex;
      }
      
      .searchTerm {
        width: 100%;
        border: 3px solid #00B4CC;
        border-right: none;
        padding: 5px;
        height: 30px;
        outline: none;
        border-radius: 5px 5px 5px 5px;
        color: #9DBFAF;
      }
      
      .searchTerm:focus{
        color: #00B4CC;
      }
      
      .searchButton {
        width: 40px;
        height: 36px;
        border: 5px solid #00B4CC;
        background: #00B4CC;
        text-align: center;
        color: #fff;
        border-radius: 0 5px 5px 0;
        cursor: pointer;
        font-size: 20px;
      }
      
      /*Resize the wrap to see the search bar change!*/
      .wrap{
        width: 30%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      `,
      <div className="search">
        <input type="text"  className="searchTerm"
           placeholder="Search " onChange={this.handleChange} onKeyPress={this.handleKeyPress}
            value={action} />
         <img src={searchlogo} className="searchButton" 
          onClick={this.getSearchResults}/>
      </div>
    )
  }
}

export default Input