import  {INCREMENT, DECREASE, SELECTED_CATEGORY} from "./actionTypes";

  export function addCategory(text) {
    return{
      type: SELECTED_CATEGORY,
      category: text,
      text
    };
  }
  
  export function incrementAction(val) {
    return{
      parameter: val,
      type: INCREMENT,
      val
    };
  }
  
export function decreaseAction() {
  return{
    type: DECREASE,
  };
}


