import React, { Component } from 'react';
import styled from 'styled-components';
import NewsItem from './NewsItem';
import axios from 'axios';
import { NavLink } from 'react-router-dom';
import Select from 'react-select';

const NewsListBlock = styled.div`
  box-sizing: border-box;
  padding-bottom: 3rem;
  width: 768px;
  margin: 0 auto;
  margin-top: 2rem;
  @media screen and (max-width: 768px) {
    width: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
  }
`;
class NewsList extends Component {
  state = {
    topics: [],
    loading: false,
    articles: null,
    selectedOption: "all",
    queries: []
  };

  getTopics = () => {
    axios.get('/api/topic')
      .then(res => {
        if(res.data){
           console.log(res.data)
           this.setState({
            topics: res.data,
          })
        }
      })
      .catch(err => console.log(err))
  }  

  handleChange = selectedOption => {
    
    console.log(`Option selected:`, selectedOption);
    this.loadData(selectedOption.value)
  };
  
  loadData = async (selectedTopic) => {
    const { category } = this.props;
    console.log(category)
    try {
      this.setState({
        loading: true
      });
      
      const response = await axios.get(
        '/api/feeds', {
          params: {
            mode: category,
            topic: selectedTopic
          }
        });
       let responseData =  response.data;
       let jsonRespose = JSON.parse(responseData)
      console.log(jsonRespose)
      this.setState({
        articles: jsonRespose['articles']
      });
    } catch (e) {
      console.log(e);
    }
    this.setState({
      loading: false
    });
  };

   setCat = (e) => {
    //e.stopPropagation();
    //e.preventDefault();
    console.log(e)
    alert(e.MouseEvent)
     /* this.setState({
          category: cat
      });*/
    }
    
  

  componentDidMount() {
    this.loadData("all");
    this.getTopics();
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.category !== this.props.category) {
      this.loadData();
    }
  }

  
  render() {
    const { articles, loading, selectedOption, topics } = this.state;
    if (loading) {
      return <NewsListBlock>Loading...</NewsListBlock>;
    }
    var options = []
    options.push({
      value: 'all',
      label: 'all'
  })
    for(var i=0; i<topics.length; i++){
      options.push({
        value: topics[i]['action'],
        label: topics[i]['action']
    })
    }
    if (!articles) return null;
    return (
      <div>
        <Select value={selectedOption}
              onChange={this.handleChange}
              options={options} 
            />
      <NewsListBlock>
        {articles.map(a => (
          <NewsItem key={a.url} article={a} />
        ))}
      </NewsListBlock>
      </div>
    );
  }
}

export default NewsList;
