import React from 'react';
import '../App.css'
import logo from  './logo-exxaro.jpg'
import Footer from './Footer'

const ConfigTopicList = ({ todos, deleteTodo }) => {
    console.log("Todos ",todos)

    //onClick={() => deleteTodo(todo._id)}
    var styles ={
        wrap: {
          width: "40%",
          //position: "absolute",
          //top: "50%",
          //left: "50%",
          //transform: "translate(-50%, -50%)",
        },
        list: {
            textAlign: "center",

        }
       }
  return (
    
  <div >
    <ul>
      {
        todos &&
          todos.length > 0 ?
            (
              todos.map(todo => {
                return (

                  <p style={styles.list} key={todo._id} onClick={() => deleteTodo(todo._id)}>{todo.action}</p>
                )
              })
            )
            :
            (
              <li>No Topic(s) left</li>
            )
      }
      <Footer/>
    </ul>

    </div>
  )
}

export default ConfigTopicList