import React from 'react';
import styled from 'styled-components';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux'


const categories = [
  {
    name: 'all',
    text: 'all news'
  },
  {
    name: 'like',
    text: 'liked news'
  },
  
];


const CategoriesBlock = styled.div`
  text-align: center;
  padding: 0.8rem;
  padding-left: 18rem;
  display: flex;
  margin: 0 auto;
  @media screen and (max-width: 768px) {
    width: 100%;
    overflow-x: auto;
  }
`;

const Category = styled(NavLink)`
  text-align: center;
  padding-bottom: 0.25rem;
  font-size: 1.125rem;
  cursor: pointer;
  white-space: pre;
  text-decoration: none;
  color: inherit;

  &.active {
    font-weight: 600;
    border-bottom: 2px solid #22b8cf;
    color: #22b8cf;
  }

  &:not(.active):hover {
    color: #495057;
  }

  & + & {
    margin-left: 1rem;
  }
`;

// to={`/${c.name === 'all' ? '' : c.name}`}
//exact={c.name === 'all'}
const Categories = ({ dispatch}) => {

  return (
    <CategoriesBlock>
      {categories.map(c => (
        <Category
          key={c.name}
          to={`/feed/${c.name === 'all' ? 'all' : c.name}`}
          activeClassName="active"
          
        >
          {c.text}
        </Category>
      ))}
    </CategoriesBlock>
  );
};

export default connect()(Categories);
