import React, {Component} from 'react';
import axios from 'axios';
import Header from '../Header';
import  { PureComponent } from 'react';
import { connect } from 'react-redux';
import ConfigInput from './ConfigInput';
import ConfigTopicList from './ConfigTopicList';
import {
  incrementAction,
  decreaseAction,
} from '../actions';


class Config extends Component {

  state = {
    todos: []
  }

  componentDidMount(){
    this.getTodos();
  }

  getTodos = () => {
    axios.get('/api/topic')
      .then(res => {
        if(res.data){
           this.setState({
            todos: res.data
          })
        }
      })
      .catch(err => alert("Trouble Connecting Server.."))
  }

  deleteTodo = (id) => {

    axios.delete(`/api/topic/${id}`)
      .then(res => {
        if(res.data){
          this.getTodos()
        }
      })
      .catch(err => console.log(err))
  }
/*
 <div>
          <p>This is value</p>
          <br/>
          <h1>{value}</h1>
          <button onClick={incrementAction(5)}>increment</button>
          <button onClick={decreaseAction}>decrease</button>
        </div>
*/
// <h1>Config ..</h1>
  render() {
    let { todos } = this.state;
    console.log(todos)

   // const { value, incrementAction, decreaseAction } = this.props;
    return(
      <div>
        <Header/>
        <div>
        <h1>Add Topic</h1>
        <ConfigInput getTodos={this.getTodos}/>
        <ConfigTopicList todos={todos} deleteTodo={this.deleteTodo}/>
      </div>
      </div>
    )
  }
}


const mapStateToProps = (state) => ({
  value: state.value,
})

const mapDispatchToProps = (dispatch) => ({
  incrementAction: (val) =>() => dispatch(incrementAction(val)),
  decreaseAction: () => dispatch(decreaseAction()),
});

export default connect(mapStateToProps, mapDispatchToProps)(Config);
