import React from 'react';
import SearchResults from './SearchResults'
import logo from  './logo-exxaro.jpg'
import Footer from './Footer'

const ListTodo = ({ todos, deleteTodo }) => {
  console.log(todos)
 
  
  var styles ={
    wrap: {
      width: "40%",
      //position: "absolute",
      //top: "50%",
      //left: "50%",
      //transform: "translate(-50%, -50%)",
    },
   }
  return (
    <div>
    <ul>
      {
        todos &&
          todos.length > 0 ?
            (
              todos.map(todo => {
                return (
                    <SearchResults key = {todo.id} title={todo.name} text={todo.snippet} 
                    link={todo.url}
                    click={() => deleteTodo(todo._id)}/>
                )
              })
            )
            :
            (
              <div/>
            )
      }
     
   </ul>
   </div>
  )
}

export default ListTodo