import React, {Component} from 'react';
import axios from 'axios';
import { connect } from 'react-redux'
import { addCategory } from '../actions'
import Input from './Input';
import ListTodo from './ListTodo';
import Iframe from 'react-iframe'
import Header from '../Header'
import PropTypes from 'prop-types'
import AnalysisCategory from './AnalysisCategory';
import Footer from './Footer'
import Select from 'react-select';
import { throws } from 'assert';
import { Chart } from "react-charts";
 


class Analysis extends Component {

  
  state = {
    topics: [],
    selectedOption: {value:"Startups"},
    sentimnentScores: [],
    Ldaurl: "Startups",
    loading: true,
    isDefault: true
  }

  static propTypes = {
    dispatch: PropTypes.func,
  }
 
  componentDidMount(){
    this.getTopics();
    this.getSentimentScore(this.state.selectedOption)
  }

 
  getSentimentScore = (selectedOption) => {
    console.log("GRETTING SENTIMENTAL SCORE ", this.state.Ldaurl)
    axios.get('/api/sentiment',  {
      params: {
        topic: selectedOption['value']
      }
    })
      .then(res => {
        if(res.data){
           this.setState({
            sentimnentScores: res.data
          })
        }
      })
      .catch(err => alert("Trouble Connecting Server"))
  }


  getTopics = () => {
    axios.get('/api/topic')
      .then(res => {
        if(res.data){
           console.log(res.data)
           this.setState({
            topics: res.data,
            loading: false
          })
        }
      })
      .catch(err => console.log(err))
  }  

  handleChange = selectedOption => {
    this.setState({ selectedOption,
      Ldaurl: selectedOption['value'],
      isDefault: false
     });
    console.log(`Option selected:`, selectedOption);
    this.getSentimentScore(selectedOption)
    console.log("SENT SCORES")
    console.log("nmnnm", this.state.sentimnentScores, this.state.sentimnentScores.length)
  };

  render() {
    const { selectedOption } = this.state;
    var styles ={
      wrap: {
        width: "40%",
        float: "center"
        //position: "absolute",
        //top: "50%",
        //left: "50%",
        //transform: "translate(-50%, -50%)",
      },
     }
     //isDefault = {this.state.isDefault}
    let { topics } = this.state;
    let { Ldaurl} = this.state;
    let {loading} = this.state
    let defaultTopic = topics[0]
    if(defaultTopic!=undefined)
    {
    console.log(defaultTopic['action'])
    }
    console.log("hahaha "+ this.state.Ldaurl)
    //
    let html_url = "http://localhost:5000/api/lda?topic=" + Ldaurl
    console.log(html_url)
    var iframe_div =  <div>
                          <Iframe 
                           url = {html_url}
                            width="850px"
                            height="850px"
                            id="myId"
                            className="myClassname"
                            display="initial"
                            position="relative"/>
                        </div>  

let scores_List = []
let count=0
this.state.sentimnentScores.map(sent => {
  scores_List.push( [count, parseFloat(sent['score']) ] )
  count+=1
})
console.log(scores_List)
  const lineChart = (
    // A react-chart hyper-responsively and continuusly fills the available
    // space of its parent element automatically
    <div
      style={{
        width: "800px",
        height: "300px"
      }}
    >
      <Chart
        data={[
          {
            label: "Series 1",
            data: scores_List//[[0, -0.32], [1, 0.21], [2, -0.312], [3, 0.2], [4, 0.7]]
          }
        ]}
        axes={[
          { primary: true, type: "linear", position: "bottom" },
          { type: "linear", position: "left" }
        ]}
      />
    </div>
  );


  
    //const { value, incrementAction } = this.props;
    const { value, addCategory} = this.props;
    var options = []
    for(var i=0; i<topics.length; i++){
      options.push({
        value: topics[i]['action'],
        label: topics[i]['action']
    })
    }
    if(loading)
    return (
      <div>
      <Header/>
          <div>
            <p>Loading...</p>
          </div>
          <Footer/>
      </div>
    )
    //var input_category = 
    return(
       
        <div>
          <Header/>
            <br/>
            <h2>Selected Topic: {value}</h2>
            <Select value={selectedOption}
              onChange={this.handleChange}
              options={options} 
            />
            <br/>
            <center>
            <h3>Analysis of {this.state.Ldaurl}</h3>
            </center>     
          {iframe_div}
          <br></br>
          <center>
            <h3>Hourly Sentimental Score of {this.state.Ldaurl}</h3>
          </center>
          {lineChart }
         <Footer/>
        </div>
    )
  }
}

// <SimpleChart isDefault = {this.state.isDefault} sentimnentScores ={this.state.sentimnentScores} />
         
const mapStateToProps = (state) => ({
  value: state.category,
})

const mapDispatchToProps = (dispatch) => ({
  addCategory: (val) =>() => dispatch(addCategory(val)),
});


export default connect(mapStateToProps, mapDispatchToProps)(Analysis);
