import React, { Component } from 'react';
import styled from 'styled-components';
import likesrc from '../imgs/like2.png'
import unlikesrc from '../imgs/like1.png'
import axios from 'axios';

class NewsItem extends Component {

  state = {
    likeStatus: false
  }


  onLikeClick = () =>{
    console.log("Got clicked ")
    var currentLike = this.state.likeStatus
    this.setState({
      likeStatus: !currentLike
    })
    var article = this.props.article
    article.like = !article.like
   //do something
  
   axios.post('/api/feeds', article)
   .then(res => {
     console.log("Done")
   })
   .catch(err => console.log(err))
  }

render() {
 let NewsItemBlock = styled.div`
  display: flex;

  .thumbnail {
    margin-right: 1rem;
    img {
      margin-top: 1.3rem;
      display: block;
      width: 180px;
      height: 180px;
      object-fit: cover;
      border-radius: 10px 10px 10px 10px;
    }
  }
  .likeButton2 {
    margin-right: 1rem;
    img {
      width: 50px;
      height: 50px;
    }
  }
  .unlikeButton2 {
    margin-right: 1rem;
    img {
      width: 30px;
      height: 30px;
      padding: 10px;
    }
  }
  .contents {
    h3 {
      a {
        color: black;
      }
      margin: 0;
    }
    p {
      margin: 0;
      line-height: 1.5;
      margin-top: 0.5rem;
      white-space: normal;
    }
    
  }
  & + & {
    margin-top: 3rem;
  }
`;

  const { title, description, url, urlToImage, like } = this.props.article;
  //let like = this.state.likeStatus
  return (
    <NewsItemBlock>
      
      {urlToImage && (
        <div className="thumbnail">
          <a href={url} target="_blank" rel="noopener noreferrer">
            <img src={urlToImage} alt="thumbnail" />
          </a>
        </div>
      )}
      <div className="contents">
        <h3 >
          <a href={url} target="_blank" rel="noopener noreferrer">
            {title}
          </a>
        </h3>
        <p>{description}</p>
      </div>
      {
        (like) &&(<div className="likeButton2">
            <img src ={likesrc} onClick={this.onLikeClick} />
          </div>
       )}
       {
        (!like) &&(<div className="unlikeButton2">
            <img src ={unlikesrc}  onClick={this.onLikeClick} />
          </div>
       )}
    </NewsItemBlock>
  );

  }
}
export default NewsItem;
